import Vue from 'vue'
import Router from 'vue-router'
import Home from '../views/home'
import Me from '../views/me'
import Like from '../views/like'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
      children: [
        {
          path: '/me',
          name: 'Me',
          component: Me
        },
        {
          path: '/like',
          name: 'like',
          component: Like
        }
      ]
    },
    {
      path: '*',
      redirect: '/'
    }
  ]
})



